<template>
  <div class="cigarettePrices-box">
    <h4>香烟查询</h4>
    <el-input v-model="param.name" placeholder="请输入香烟品牌"></el-input>
    <el-button :type="buttonType" :loading="loading" @click="reqList()">{{
      buttonValue
    }}</el-button>
    <div class="cigarettePrices-list">
      <ul>
        <li v-for="(item, index) in CigarettePricesList" :key="index">
          <h6>{{ item.name }}</h6>
          <p>长度：{{ item.long_length }}</p>
          <p>香烟类型：{{ item.type }}</p>
          <p>价格/条：{{ item.whole_price }}元</p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "CigarettePrices",
  data() {
    return {
      CigarettePricesList: [],
      param: {
        name: "",
      },
      buttonType: "primary",
      buttonValue: "点击查询",
      loading: false,
    };
  },
  methods: {
    reqList() {
      this.loading = true;
      const params = this.param;
      axios
        .get("cigarette", {
          params,
        })
        .then((response) => {
          if (response.data.msg == "查询成功") {
            this.CigarettePricesList = response.data.data;
            this.loading = false;
            // this.buttonValue = "查询成功";
            // this.buttonType = "success";
          }
        })
        .catch((error) => {
          console.log("请求失败".error);
          this.buttonValue = "请求失败";
          this.buttonType = "warning";
        });
    },
  },
};
</script>

<style lang="less" scoped>
.cigarettePrices-box {
  width: 1200px;
  margin: 0 auto;
  h4 {
    margin-bottom: 30px;
  }
  .el-input {
    width: 30%;
    margin-right: 20px;
  }
  .cigarettePrices-list {
    ul {
      display: flex;
      flex-wrap: wrap;

      li {
        width: 33%;
        margin-top: 15px;
        h6 {
          font-size: 18px;
          line-height: 36px;
        }
      }
      span {
        font-size: 16px;
        line-height: 32px;
      }
    }
  }
}
</style>