import { reqDouYin } from '@/api'
import { reqDailyNewList } from '@/api/index'
import { reqCarouselImg } from '@/api/index'
import { reqAddClipboard } from '@/api'




// home的小仓库

// 小仓库存储数据的地方
const state = {
    // 设置categoryList的初始值是一个数组，根据接口的返回值去初始化，是数组的就初始化为数组，是对象的就初始化为对象
    douYinList: {},
    dailyNewList: {},
    carouselImg: {},
    pasteList: [],
}

// 唯一修改state的地方
const mutations = {
    GETDOUYINLIST(state, douYinList) {
        state.douYinList = douYinList
    },

    GETDAILYNEWLIST(state, dailyNewList) {
        state.dailyNewList = dailyNewList
    },

    GETCAROUSELIMG(state, carouselImg) {
        state.carouselImg = carouselImg
    }
}

// 响应组件中的动作
const actions = {
    // 通过api里的接口函数调用，向服务器发请求，获取服务器的数据
    async getDouYinList({ commit }) {  // async：异步  必须和await搭配使用  commit是VUEX库中的一个方法，用于提交一个mutation来修改应用程序的状态
        // 定义一个result，此时它是一个对象，包含code、message、data、ok等
        let result = await reqDouYin();
        if (result.status == 200) {
            // 提交mutation
            commit("GETDOUYINLIST", result.data.data)
            // commit：VUE传递多个参数的方法
        }
    },

    // 调用每日新闻api
    async getDailyNewList({ commit }, params) {  // async：异步  必须和await搭配使用  commit是VUEX库中的一个方法，用于提交一个mutation来修改应用程序的状态
        // 定义一个result，此时它是一个对象，包含code、message、data、ok等
        let result = await reqDailyNewList(params);
        if (result.status == 200) {
            // 提交mutation
            commit("GETDAILYNEWLIST", result.data.data)
            // commit：VUE传递多个参数的方法
        }
    },

    // 调用轮播图接口
    async getCarouselImg({ commit }) {  // async：异步  必须和await搭配使用  commit是VUEX库中的一个方法，用于提交一个mutation来修改应用程序的状态
        // 定义一个result，此时它是一个对象，包含code、message、data、ok等
        let result = await reqCarouselImg();
        // console.log(result);
        if (result.data.code == 1) {
            // 提交mutation
            commit("GETCAROUSELIMG", result.data.result)
            // console.log("请求成功");
        }
    },


    async getPasteList({ commit }, params) {
        // console.log(params);
        let result = await reqAddClipboard(params)
        console.log(result);
    }
}

// 加工数据的地方（为了简化仓库中的数据而生）
const getters = {

}

// 对外暴露
export default {
    state,
    mutations,
    actions,
    getters
}



// 拿到接口中的数据步骤
// 1. 先进行初始化 categoryList:[]
// 2. 通过actions去响应组件中的动作，调用categoryList方法，在方法中定义 result 为 reqCategoryList对象，包含code、message、data、ok等
// 3. 再用commit传递 CATGORYLIST 和 result.data（接口获取到的数据）
// 4. 再mutations中，使用commit 传递的 CATGORYLIST 来接受数据，讲接受到的数据赋值给 state中的数组categoryList

