<template>
  <div class="GetApiList-box">
    <!-- 逆天言论 -->
    <div class="DouYinVideo">
      <el-button @click="newList">点击请求发送请求</el-button>
      <div class="List-box">{{ douYinList.msg }}</div>
      <br />
    </div>
    <!-- 每日新闻 -->
    <div class="DailyNewList">
      <div class="button-box" @click="dailyList($event)">
        <el-button :type="this.bilibili.type">哔哩哔哩</el-button>
        <el-button :type="this.baidu.type">百度</el-button>
      </div>
      <div class="new-List-box">
        <el-button @click="nextPag()">换一换</el-button>
        <ul>
          <li v-for="(item, index) in displayedItems" :key="index">
            <a :href="item.mobileUrl">
              {{ currenIndex + index + 1 + "." + item.title }}
            </a>
          </li>
        </ul>
      </div>
      <br />
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex"; // 引入 mapState 方法
export default {
  name: "GetApiList",
  data() {
    return {
      param: {
        title: "哔哩哔哩",
      },
      bilibili: {
        type: "",
        loading: true,
      },
      baidu: {
        type: "",
        loading: true,
      },
      currenIndex: 0,
      pageSize: 20, // 每页展示的条数
      displayedItems: [], // 保存当前展示信息的数组
    };
  },
  methods: {
    newList() {
      // 获取数据渲染到组件 第一步   调用在store/getApiList/index.js中定义的方法,调用的同时可以传入参数
      this.$store.dispatch("getDouYinList");
    },
    dailyList($event) {
      this.param.title = $event.target.innerText;
      this.currenIndex = 0;
      if (this.param.title == "哔哩哔哩") {
        this.bilibili.type = "primary";
        this.baidu.type = "";
      } else if (this.param.title == "百度") {
        this.bilibili.type = "";
        this.baidu.type = "primary";
      }
      this.$store.dispatch("getDailyNewList", this.param);
      this.loadMore();
    },
    // 判断当前应该展示那些信息
    nextPag() {
      if (
        this.currenIndex + this.pageSize >= 100 || // 展示的信息超过100条
        this.currenIndex + this.pageSize >= this.dailyNewList.length //或者超过api获取的信息总条数
      ) {
        this.currenIndex = 0; // 从0开始展示
      } else {
        this.currenIndex += this.pageSize; // 否则，移动到下一批
      }
      this.loadMore();
    },
    // 查看更多新闻列表
    loadMore() {
      this.displayedItems = this.dailyNewList.slice(
        this.currenIndex,
        this.currenIndex + this.pageSize
      );
    },
  },
  mounted() {
    this.newList(); // 将该方法放到生命周期中，为了在页面刚加载时调用api
  },

  // 获取数据渲染到组件 第二步 此时已经可以将获取到的数据渲染到组件上了
  computed: {
    ...mapState({
      // 这里是去store/getApiList/index.js文件中去找数组
      douYinList: (state) => state.getApiList.douYinList,
      dailyNewList: (state) => state.getApiList.dailyNewList,
    }),
  },
};
</script>

<style lang="less" scoped>
.GetApiList-box {
  width: 1200px;
  margin: 0 auto;
  .List-box {
    font-size: 16px;
    line-height: 32px;
    text-indent: 2rem;
    margin-top: 30px;
  }
  .DailyNewList {
    .new-List-box {
      ul {
        margin-top: 30px;
        li {
          a {
            font-size: 16px;
            line-height: 32px;
          }
        }
      }
    }
  }
}
</style>