<template>
  <div>
    <h2>组件通信的子组件2</h2>
    <strong>{{ val }}</strong
    >，不推荐使用
  </div>
</template>

<script>
export default {
  name: "twoListListChile2",
  data() {
    return {
      title: "子组件传递给父组件的数据",
      title2: "子组件传递给兄弟组件的数据",
    };
  },
  inject: ["val"],
  mounted() {
    this.$emit("obtainChileTitle", this.title);
    this.$bus.$emit("emitTitle2", this.title2);
  },
};
</script>

<style lang="less" scoped>
* {
  text-align: center;
}
</style>