<template>
  <div class="clipboard">
    <h1>粘贴板</h1>
    <div class="upload">
      <el-input
        v-model="input"
        placeholder="请输入内容"
        type="textarea"
        :rows="5"
        clearable
      ></el-input>
      <el-button type="primary" @click="uploadData()">提交</el-button>
    </div>
    <div class="obtain" v-if="copiedText.length > 0">
      <p v-for="(text, index) in copiedText" :key="index">
        {{ text.title }}
        <el-button
          type="primary"
          icon="el-icon-delete"
          class="delete-btn"
        ></el-button>
      </p>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapState } from "vuex";
export default {
  name: "Clipboard",
  data() {
    return {
      // 输入
      input: "",
      params: {
        id: "",
        title: "",
      },
      // 获取
      primaryInput: {
        id: "",
        title: "",
      },
      copiedText: [],
    };
  },
  mounted() {
    this.pasteText();
  },
  methods: {
    uploadData() {
      this.params.title = this.input;
      // this.$store.dispatch("getPasteList", this.params);
      // this.pasteText();
      // this.copyText();
      axios
        .get("http://45.95.175.123:3322/copy", {
          params: {
            text: this.input,
          },
        })
        .then((response) => {
          console.log(response.data);
        })
        .catch((error) => {
          console.error("Error copying text:", error);
        });
    },
    // copyText() {
    //   // ...mapState({
    //   //   copiedText:(state) => state.pasteList
    //   // })
    //   axios
    //     .get("http://45.95.175.123:3322/copy", {
    //       params: {
    //         text: this.input,
    //       },
    //     })
    //     .then((response) => {
    //       console.log(response.data);
    //     })
    //     .catch((error) => {
    //       console.error("Error copying text:", error);
    //     });
    // },
    pasteText() {
      axios
        .get("http://45.95.175.123:3322/paste")
        .then((response) => {
          if (response.data && response.data.length > 0) {
            this.copiedText = response.data;
          } else {
            this.copiedText = [];
          }
        })
        .catch((error) => {
          console.error("Error pasting text:", error);
        });
    },
  },
};
</script>

<style lang="less" scoped>
.clipboard {
  width: 1200px;
  margin: 0 auto;
  h1 {
    text-align: center;
    margin-bottom: 30px;
  }
  .upload {
    display: flex;
  }
  .obtain {
    margin: 30px 0;
    padding: 15px;
    min-height: 200px;
    border: 1px solid #000;
    p {
      position: relative;
      display: flex;
      justify-content: space-between;
      font-size: 16px;
      line-height: 2;
      padding: 10px;
      margin-bottom: 30px;
      background: #f9f9f9;
      overflow: hidden;
      .delete-btn {
        position: absolute;
        top: 0;
        right: -50px;
        width: 50px;
        height: 100%;
        background-color: red;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: right 0.3s ease; /* 使用 CSS 过渡动画 */
      }

      .delete-btn.show {
        right: 0;
      }
    }
  }
}
</style>