<template>
  <dir>
    <h1>关于vue自带的API</h1>
    <button @click="btn()">按钮一</button>
    <button @click="btn2()">按钮二</button>
    <div>{{ arr }}</div>
    <button @click="logEl">log$el</button>
  </dir>
</template>

<script>
export default {
  name: "fourList",
  data() {
    return {
      arr: ["aaa", "bbb", "ccc"],
    };
  },
  methods: {
    btn() {
      // 直接使用 this.arr  这种写法只能修改数据，但是视图不会跟着变
      this.arr[0] = "xxx";
      console.log(this.arr);
    },
    btn2() {
      // 直接使用 this.arr  这种写法只能修改数据，但是视图不会跟着变
      this.$set(this.arr, "0", "xxx");
      console.log(this.arr);
    },
    logEl() {
      console.log(this.$el);
    },
  },
};
</script>

