<template>
  <div>
    <h4>oneList的子组件</h4>
    {{ info }}
  </div>
</template>

<script>
export default {
  name: "oneListChile",
  data() {
    return {
      title: "子组件",
    };
  },
  props: ["info"],
  beforeCreate() {
    console.log("子组件的beforeCreate", this.$el, this.$data);
  },
  created() {
    console.log("子组件的created", this.$el, this.$data, this.info);
  },
  beforeMount() {
    console.log("子组件的beforeMount", this.$el, this.$data);
  },
  mounted() {
    console.log("子组件的mounted", this.$el, this.$data);
  },
  beforeUpdate() {
    console.log("子组件的beforeUpdate", this.$el, this.$data);
  },
  updated() {
    console.log("子组件的updated", this.$el, this.$data);
  },
  beforeDestroy() {
    console.log("子组件的beforeDestroy", this.$el, this.$data);
  },
  destroyed() {
    console.log("子组件的destroyed", this.$el, this.$data);
  },
};
</script>

<style lang="less" scoped>
* {
  text-align: center;
}
</style>