<template>
  <div>
    <h2>生命周期函数</h2>
    {{ num }}
    <button @click="addNum">+1</button>
    <br />
    <oneListChile :info="[infotitle, num]" />
  </div>
</template>

<script>
import oneListChile from "@/pages/Aceshi/oneList/oneListChile";
export default {
  name: "oneList",
  data() {
    return {
      title: "父组件",
      infotitle: "传递的值",
      num: 1,
    };
  },
  components: {
    oneListChile,
  },
  beforeCreate() {
    console.log("父组件的beforeCreate", this.$el, this.$data);
  },
  created() {
    console.log("父组件的created", this.$el, this.$data);
  },
  beforeMount() {
    console.log("父组件的beforeMount", this.$el, this.$data);
  },
  mounted() {
    console.log("父组件的mounted", this.$el, this.$data);
  },
  beforeUpdate() {
    console.log("父组件的beforeUpdate", this.$el, this.$data);
  },
  updated() {
    console.log("父组件的updated", this.$el, this.$data);
  },
  beforeDestroy() {
    console.log("父组件的beforeDestroy", this.$el, this.$data);
  },
  destroyed() {
    console.log("父组件的destroyed", this.$el, this.$data);
  },
  methods: {
    addNum() {
      this.num++;
    },
  },
};
</script>

<style lang="less" scoped>
* {
  text-align: center;
}
</style>