<template>
  <div>
    <h1>插槽和持久化存储</h1>
    <h1>插槽部分</h1>
    <hr />
    <threeListChile>
      <div>默认插槽：插槽插入的内容</div>
      <div slot="one">具名插槽：插入的内容</div>
    </threeListChile>
    <div>-----------父组件的分割线</div>

    <br />
    <threeListChile2 />

    <h1>持久化存储部分</h1>
    <hr />
    <button @click="addToken()">点击存储到Token</button>
    <br />
    <button @click="removeToken()">点击删除Token</button>
    <br />
    <button @click="getToken()">点击读取Token</button>
    <br />
  </div>
</template>

<script>
import threeListChile from "@/pages/Aceshi/threeList/threeListChile";
import threeListChile2 from "@/pages/Aceshi/threeList/threeListChile2";

import { setToken, getToken, removeToken } from "@/utils/token";
export default {
  name: "threeList",
  data() {
    return {
      token: "LYH",
    };
  },
  components: {
    threeListChile,
    threeListChile2,
  },
  methods: {
    // 添加本地存储
    addToken() {
      setToken(this.token);
    },
    // 删除本地存储
    removeToken() {
      removeToken();
    },
    // 获取本地存储
    getToken() {
      alert(localStorage.getItem("TOKEN"));
    },
  },
};
</script>

<style>
* {
  text-align: center;
}
</style>