<template>
  <div>
    <h2>组件通信的子组件</h2>
    <div>
      props传递：<strong>{{ info }}</strong>
    </div>
    <div>
      使用this.$parent：<strong>{{ this.$parent.num }}</strong
      >，不推荐使用
    </div>
    <button @click="addParent()">++</button>
    <br />
    <strong>{{ val }}</strong
    >不推荐使用
    <br />
    <div>
      使用$bus获取到兄弟组件的数据<strong>{{ title2 }}</strong>
    </div>
  </div>
</template>

<script>
export default {
  name: "twoListListChile",
  data() {
    return {
      title: "子组件",
      title2: "",
    };
  },
  props: ["info"],
  methods: {
    addParent() {
      this.$parent.num++;
    },
  },
  inject: ["val"],
  mounted() {
    this.$bus.$on("emitTitle2", (title2) => {
      this.title2 = title2;
    });
  },
  beforeDestroy() {
    this.$bus.$off("emitTitle2");
  },
};
</script>

<style lang="less" scoped>
* {
  text-align: center;
}
</style>