<template>
  <div>
    <h1>组件通信</h1>
    <hr />
    <twoListListChile :info="num" />
    <hr />
    <twoListListChile2 @obtainChileTitle="chileTitle" ref="chile2" />
    <hr />
    <div>自定义事件获取子组件的数据：{{ infotitle }}</div>
  </div>
</template>

<script>
import twoListListChile from "@/pages/Aceshi/twoList/twoListListChile";
import twoListListChile2 from "@/pages/Aceshi/twoList/twoListListChile2";
export default {
  name: "twoList",
  data() {
    return {
      title: "父组件",
      infotitle: "传递的值",
      num: 1,
    };
  },
  components: {
    twoListListChile,
    twoListListChile2,
  },
  provide() {
    return {
      val: "父组件的依赖注入",
    };
  },
  methods: {
    chileTitle(infotitle) {
      this.infotitle = infotitle;
    },
  },
  mounted() {
    console.log(
      "使用this.$refs.xxx.xxx获取到子组件的数据",
      this.$refs.chile2.title,
      "不推荐使用",
      "使用this.$children获取到子组件的数据",
      this.$children[1].title
    );
  },
};
</script>

<style lang="less" scoped>
* {
  text-align: center;
}
</style>