// 当前这个模块，API进行统一管理
import requests from "./request";

// 请求逆天言论接口
export const reqDouYin = () => {
    return requests({
        'url': 'ntyy',  // 请求接口地址
        // 'url': 'https://api.woeo.net/API/api-girl/index.php',
        method: 'GET',  // 请求方式
        // headers: {  // 请求头信息
        //     "Content-Type": "video/mp4",
        // }
    })
}

// 请求每日新闻接口
export const reqDailyNewList = (params) => {
    return requests({
        'url': 'dailyhot',
        method: 'GET',
        // 请求参数  get请求时传入参数是params，post请求传入参数是data。键值一致时可以简写只保留一个
        params,
        // 数据类型
        dataType: "json"
    })
}

// 请求ai讲故事接口
export const reqAiStoryList = (params) => {
    return requests({
        'url': '/api/rolechat/send',
        method: 'GET',
        // 请求参数  get请求时传入参数是params，post请求传入参数是data。键值一致时可以简写只保留一个
        params,
        headers: {  // 该接口必须接待这些请求头
            // "Host":"<calculated when request is sent>",
            // "User-Agent":"PostmanRuntime/7.36.1",
            // "Accept":"*/*",
            // "Accept-Encoding":"gzip, deflate, br",
            // "Connection":"keep-alive",
            // "Content-Type":"application/x-www-form-urlencoded;charset=UTF-8",

            'Wid': '1',
            'Token': '25d28cca-aea3-11ee-9f65-0242d1ee50c3'
        },
        dataType: "json",
    })
}

